/* --------------------------------- Layout ---------------------------------*/

.container {
  width: 320px;
  padding: 12px;
}

/* vertically center .container */
body {
  min-height: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
}

button {
  width: 65px;
  height: 45px;
  font-family: 'Roboto', sans-serif;
  border: none;
  border-radius: 5px;
  outline: none !important;
  position:relative;
  transition: all 0.1s;
}

button.operator-group, button#equal {
  font-size: 30px !important;
}

button.operand-group, button#clear, button#ans {
  font-size: 20px !important;
}

#display1 {
  font-family: 'Roboto', sans-serif;
  font-weight: 400 !important;
  font-size: 20px !important;
  height: 50px;
}

.button-row {
  margin-top: 12px;
}

form {
  width: 294px;
  border-radius: .25rem;
}

form input {
  background-color: inherit;
  border: none;
  width: 294px;
  margin: 0 auto;
  display: block;
}

