/*----------------------------- Rounded switch ------------------------------*/

.switch input {
  display: none;
}

.switch {
  position: relative;
  width: 30px;
  height: 17px;
  display: inline-block;
  margin-bottom: 5px;
}

.slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: pointer;
  transition: .4s;
  border-radius: 50px;
  background-color: #263238;
}

.slider:before {
  position: absolute;
  content: "";
  height: 13px;
  width: 13px;
  left: 2px;
  bottom: 2px;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .slider:before {
  background-color: #263238;
}

input:not(:checked) + .slider:before {
  background-color: #eceff1;
}

input:checked + .slider {
  background-color: #eceff1;
}

input:focus + .slider {
  box-shadow: 0 0 1px #eceff1;
}

input:checked + .slider:before {
  transform: translateX(13px);
}
